@import url(https://fonts.googleapis.com/css?family=Raleway:300,400,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  height: 100%;
}


*,*::after,*::before{margin:0;padding:0;box-sizing:inherit;font-family:inherit}html{font-size:62.5%}body{line-height:1;box-sizing:border-box;overflow-x:hidden;color:#404240;font-size:1.6rem;font-family:"Raleway", sans-serif;background-color:#d7d7d7;letter-spacing:0.15rem}html,body{height:100%}.app-container{height:100%;display:flex;align-items:center;justify-content:center}.inline-wrapper{display:flex;align-items:center;justify-content:space-between;margin-bottom:1.5rem}.card{width:38rem;border-radius:0.5rem;overflow:hidden;box-shadow:3px 1px 15px 1px rgba(0,0,0,0.2);transition:all 0.5s linear;margin:1rem}.card:hover{transform:translateY(-0.5rem);box-shadow:5px 5px 15px 3px rgba(0,0,0,0.2)}.card:hover .card__header img{transform:scale(1.1);-webkit-filter:grayscale(0%);filter:grayscale(0%)}.card--light{background-color:#fff}.card--dark{background-color:#262626;color:#fff}.card--dark h2{border-bottom-color:#fff}.card__header{overflow:hidden;height:25rem;position:relative}.card__header img{position:relative;height:100%;width:100%;object-fit:cover;object-position:center;transition:all 0.4s ease-in-out;-webkit-filter:grayscale(50%);filter:grayscale(50%)}.card__close-btn{position:absolute;top:0;right:0;height:3rem;width:3rem;background-color:#000;color:#fff;display:flex;align-items:center;justify-content:center;z-index:1;cursor:pointer}.card__wrapper{padding:2rem 2.5rem}.card__body{line-height:1.3}.card__body i+*{color:#ff4e8a;font-weight:bold}.card__title{color:#ff4e8a;font-weight:700;border-bottom:1px solid rgba(64,66,64,0.4);padding-bottom:2rem;margin-bottom:2rem}.card__footer{display:block;text-align:center;margin-top:2rem}.card__button{border:0;background:#ff4e8a;padding:0.8rem 2.5rem;border-radius:0.5rem;font-size:1.6rem;color:#fff;font-weight:700;letter-spacing:0.3rem;cursor:pointer;transition:all 0.3s ease-in-out}.card__button:hover{background:#ff689b}

